$dev: #ffffff;

@font-face {
  font-family: 'proxima-nova';
  src: url('/assets/fonts/ProximaNova-Regular.otf');
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: 'proxima-nova';
  color: #2a2e30;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto';
  font-weight: 500;
}

p {
  font-size: 20px;
  line-height: 29px;
}

nav {
  background-color: $dev;
  padding: 25px;
  text-align: center;
  border-bottom: 1px solid #d0d7de;

  .logo {
    position: absolute;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 600;
  }


  
  li{
    list-style: none;
    display: inline-block;
  }
  a {
    color: #24292f;
    font-size: 18px;
    font-weight: 400;
    padding: 0 15px;
    text-decoration: none;
  }
  a:hover {
    text-decoration: underline;
  }
}

@media only screen and (max-width: 1200px) {
  .sidebar {
    position: relative!important;
    width: 100%!important;
    ul ul {
      padding-left: 18px!important;
    }
    }
    
    #toc {
      font-size: 18px;
      margin: 0px!important;
      padding-right: 0px;
      margin-right: 0;
      border-right: none;
    }
}

@media only screen and (max-width: 600px) {
  .logo {
    position: relative!important;
    padding-bottom: 7px;
  }
  .section-nav {
    padding-left: 20px;
  }
  .blog-item {
    padding: 0;
    li {
      list-style-type: none;
    }
  }
}



body {
  margin: 0;
}

.content {
  padding: 20px;
  background: $dev;
  max-width: 700px;
  margin: 10px auto;

  a {
    color: #2a7ae2;
    text-decoration: none;
  }
  a:visited {
    color: #1756a9;
  }
}

.sidebar {
  position: fixed;
  width: 0px;
  display: block;
  ul ul {
    padding-left: 10px;
  }
  
  }
  
  .sidebar div {
  padding: 8px;
  font-size: 24px;
  display: block;
  }

#date-author {
  font-size: 15px;
  margin-top: -15px;
}

