#toc {
  font-size: 18px;
  margin: 5px 0 0 -250px;
  padding-right: 10px;
  margin-right: 8px;
  border-right: 1px solid #d7d7d7;
}

li.toc-entry.toc-h1 {
  font-weight: bold;
}
.toc-h2, .toc-h3, .toc-h4  {
  font-weight: normal;
}

/* Tooltip from https://www.w3schools.com/css/css_tooltip.asp */
.jekyll-glossary {
  position: relative;
  // display: inline-block;
  border-bottom: 2px dotted #0074bd;
  cursor: help;
}

.jekyll-glossary .jekyll-glossary-tooltip {
  visibility: hidden;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  text-align: center;
  font-size: 0.8em;
  line-height: 1.5em;
  padding: 5px;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;

  width: 400px;
  bottom: 100%;
  left: 50%;
  margin-left: -200px; /* Use half of the width to center the tooltip */

}

/* Show the tooltip text when you mouse over the tooltip container */
.jekyll-glossary:hover .jekyll-glossary-tooltip {
  visibility: visible;
}

/* Style the source link (if there is one provided in the glossary entry). */
.jekyll-glossary-source-link:before {
  content: "[source]";  // "(reference)", "<link>" or whatever you want.
}

/* Arrow created with borders. */
.jekyll-glossary .jekyll-glossary-tooltip::after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

/* Animation from invisible to visible on hover. */
.jekyll-glossary .jekyll-glossary-tooltip {
  opacity: 0;
  transition: opacity 0.25s;
}
.jekyll-glossary:hover .jekyll-glossary-tooltip {
  opacity: 1;
}